.side-drawer {
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 70%;
    background: rgb(20, 20, 20);
    box-shadow: 0 2px 25px rgba(0, 0, 0, 0.863);
  }
  

  /* Entering State */
  .slide-in-left-enter {
    transform: translateX(-100%);
  }
  
  .slide-in-left-enter-active {
    transform: translateX(0);
    transition: transform 0.2s ease-out;
  }
  
  /* Exiting State */
  .slide-in-left-exit {
    transform: translateX(0);
  }
  
  .slide-in-left-exit-active {
    transform: translateX(-100%);
    transition: transform 0.2s ease-out;
  }