.home-container {
    margin-top: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    /* height: 80vh; */
    /* background-color: rgba(255, 23, 23, 0.308); */
}

.home-logo {
    width: 10rem;
    height: 10rem;
}

.home-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.562);
    padding: 6rem;
    overflow: hidden;
    background: rgba(26, 26, 26, 0.425);
    border-radius: 1rem;
    color: rgb(255, 255, 255);
}

.home-card-links > li {
    list-style: none;
    font-size: larger;
}

