.places {
    display: flex;
    flex-direction: row;
    gap: 5rem;
    /* flex-wrap: nowrap; */
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 23, 23, 0.308);
}

.places-container {
    background-color: rgba(0, 0, 0, 0.459);
    padding: 3rem;
    border-radius: 1.3rem;
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}