.user-item {
    margin: 1rem;
    width: calc(45% - 2rem);
    min-width: 17.5rem;
    list-style: none;
  }
  
.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  background: rgba(185, 215, 255, 0.644);
}

.user-item a:hover{
  background: #e2e2e2c4;
}
.user-item a:active {
  background: #e2e2e286;
}

.user-item__content {
  padding: 0;
}

.user-item__image {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1rem;
}

.user-item__info h2 {
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #ffd900;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}

.user-item__info h3 {
  margin: 0;
  font-size: 0.8rem;

}