.place-form {
  list-style: none;
  margin: 0 auto;
  margin-top: 5rem;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  color: aliceblue;
  background: rgb(212, 212, 212);
  /* background-image: linear-gradient(rgba(6, 0, 58, 0.486),rgb(22, 22, 22)); */
  background-image: linear-gradient(rgba(17, 8, 94, 0.767),rgba(109, 7, 112, 0.534));
}
